import { ReactComponent as GithubImg } from '../images/github.svg';

const Github = () => (
  <a
    href="https://github.com/z0ccc/locatejs"
    className="github"
    target="_blank"
    rel="noreferrer"
    alt="Link to Github page"
  >
    <GithubImg />
  </a>
);

export default Github;
